import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { withRouter,Redirect } from 'react-router';
import { compose } from 'redux';
import {setSearchResults, setSearchTextActionCreator, setPageActionCreator} from "../redux/search-reducer";
import {setTypeActionCreator} from "../redux/details-reducer";
import Card from '../components/Card';
import Pagination from '../components/Pagination';
const SearchPage = ({search, searchText, location, totalPages, setSearchTextActionCreator, setSearchResults, currentPage, setPageActionCreator, setTypeActionCreator}) => {
    // const [searchString, setSearhString] = useState('')
    useEffect(()=>{
        // console.log(location.search.length-3)        
        if(searchText===null){
            setSearchTextActionCreator(location.search.substr(3, location.search.length))
            setSearchResults(location.search.substr(3, location.search.length))
            // console.log('dsad')
        }
    },[location.search, searchText, setSearchTextActionCreator, setSearchResults])
    useEffect(()=>{
        document.title = 'Поиск'
    },[])
    return (
        <section>
            <div className="container-fluid">
                <h2 className="title">Результаты поиска по запросу <span>{searchText}</span></h2>
                <div className="main-grid">
                    {search.map(item=> {return <Card key={item.id} titleId={item.id} setType={setTypeActionCreator} type={item.type} src={item.poster_path} title={item.title || item.name} score={item.vote_average} />} )}
                </div>
                <Pagination currentPage={currentPage} pagesCount={totalPages} searchText={searchText} setSearchResults={setSearchResults} setCurrentPage={setPageActionCreator}/>
            </div>
        </section>
    );
};
const mapStateToProps = (state)=>{
    return{
        search: state.search.searchData,
        totalPages: state.search.totalPages,
        searchText: state.search.searchText,
        currentPage: state.search.page,
        titleType: state.details.titleType
    }
}
export default compose( 
    connect(mapStateToProps, {setSearchResults, setSearchTextActionCreator, setPageActionCreator, setTypeActionCreator}),
    withRouter,
)(SearchPage)