import React from 'react';
import { Link } from 'react-router-dom';
import { smallImgPath, toElementScroll, unavailable } from '../global/config';

const Card = ({ src, title, titleId,type, setType, score, clickHandler }) => {
	const onClickHandler =()=>{
		if(clickHandler){ 
			clickHandler()
		}
		toElementScroll(false)
	}
	return (
		<Link to={`/title/${type}/${titleId}`} className="card" 
			onClick={onClickHandler}
		>
			<div className="card__img">
				<img src={ src ? (smallImgPath + src): unavailable} alt="" />
			</div>
			<div className="card__name">{title}</div>
			<div className="card__score">
				{score}
			</div>
		</Link>
	);
};

export default Card;