import { toElementScroll } from "../global/config";

const Pagination = (props) => {
	const setCurrentPage = (p) => {
		props.setCurrentPage(p)
		if( props.setSearchResults){
			props.setSearchResults(props.searchText ,p)
			
		}
        if(props.setDiscoverData){
			props.setDiscoverData(props.discover, p)
		}
		// props.getItems(p, props.itemsCount)
		// document.querySelector('section').scrollIntoView({ 
		// 	behavior: 'smooth', 
		// 	block: 'start' 
		// })
		toElementScroll(true)
	}
	// const props.pagesCount = Math.ceil(props.totalItemsCount / props.itemsCount)
	const pages = []
	for (let i = Math.max(props.currentPage - 2, 2); i <= Math.max(1, Math.min(props.currentPage + 2, props.pagesCount - 1)); i++) {
		pages.push(i);
	}
	const toPrevPage = () => {
		if (props.currentPage - 1 > 0) {
			return props.currentPage - 1
		} else {
			return 0
		}
	}
	const toNextPage = () => {
		if (props.currentPage + 1 <= props.pagesCount) {
			return props.currentPage + 1
		} else {
			return props.pagesCount + 1
		}
	}
	return (
		props.pagesCount>1 &&
		<ul className="pagination">
			{/* <li><span className={toPrevPage() < 1 ? 'arrow is-disabled' : 'arrow'} onClick={() => setCurrentPage(toPrevPage())}>{'<'}</span></li>
			<li><span className={props.currentPage === 1 && 'is-active'} onClick={() => setCurrentPage(1)}>{1}</span></li>
			{pages.map(pageNum => <li key={pageNum}><span className=props.currentPage === pageNum? 'is-active':''} onClick={() => setCurrentPage(pageNum)}>{pageNum}</span></li>)}
			<li><span className={props.currentPage === props.pagesCount && 'is-active'} onClick={() => setCurrentPage(props.pagesCount)}>{props.pagesCount}</span></li>
			<li><span className={toNextPage() <= props.pagesCount ? 'arrow' : 'arrow is-disabled'} onClick={() => setCurrentPage(toNextPage())}>{'>'}</span></li> */}
			<li><span className={toPrevPage() < 1 ? 'arrow is-disabled' : 'arrow'}  onClick={() => setCurrentPage(toPrevPage())}>{''}</span></li>
			<li><span className={props.currentPage === 1 ? 'is-active': ''} onClick={() => setCurrentPage(1)}>{1}</span></li>
			{pages.map(pageNum => <li key={pageNum}><span className={props.currentPage === pageNum? 'is-active':''} onClick={() => setCurrentPage(pageNum)}>{pageNum}</span></li>)}
			<li><span className={props.currentPage === props.pagesCount ?  'is-active': ''} onClick={() => setCurrentPage(props.pagesCount)}>{props.pagesCount}</span></li>
			<li><span  className={toNextPage() <= props.pagesCount ? 'arrow' : 'arrow is-disabled'} onClick={() => setCurrentPage(toNextPage())}>{''}</span></li>
		</ul>
	);
};

export default Pagination;